function addImageToRow(elem, img, w, h) {
    let tile = document.createElement('div');
    tile.className = "tile-factorio-" + img;
    if (img == "air" || img == null) {
        tile.style.width = w.toString() + "px";
        tile.style.height = h.toString() + "px";
    }
    elem.appendChild(tile);
    return tile;
}
function addPositionedImage(elem, img, w, h, dx, dy, scale = 1) {
    let added = addImageToRow(elem, img, w, h);
    added.style.position = "absolute";
    added.style.left = dx.toString() + "px";
    added.style.top = dy.toString() + "px";
    //if (img.startsWith("geothermal"))
    //	scale *= 2;
    if (img != null && typeof (img) != "undefined" && img.startsWith("oil"))
        scale *= 1.5;
    added.style.transformOrigin = "top left";
    added.style.transform = "scale(" + scale + ")";
    if (img == "spawner")
        added.style.transform = added.style.transform + " translate(-16px)";
    return added;
}
function buildLooseImage(elem, rows, cols, grid, size, scale = 1, fuzzX = 0, fuzzY = 0) {
    var x = 0;
    var y = 0;
    for (var k = 0; k < rows; k++) {
        x = 0;
        for (var i = 0; i < cols; i++) {
            let idx = getArrayIndex(cols, i, k);
            let img = grid[idx];
            img = modifyImageLocational(grid, rows, cols, i, k, idx, img);
            if (img != "air") {
                let dx = x;
                if (fuzzX > 0) {
                    dx += (Math.random() - 0.5) * fuzzX * 2;
                }
                let dy = y;
                if (fuzzY > 0) {
                    dy += (Math.random() - 0.5) * fuzzY * 2;
                }
                addPositionedImage(elem, img, size, size, dx, dy, scale);
            }
            x += size;
        }
        y += size;
    }
    fitBCG(elem, SCALE);
}
function buildImage(elem, rows, cols, grid, scaleFactor) {
    var w = 32 * SCALE * scaleFactor;
    var h = 32 * SCALE * scaleFactor;
    var x = 0;
    var y = 0;
    for (var k = 0; k < rows; k++) {
        x = 0;
        let row = document.createElement("div");
        for (var i = 0; i < cols; i++) {
            let idx = getArrayIndex(cols, i, k);
            let img = grid[idx];
            img = modifyImageLocational(grid, rows, cols, i, k, idx, img);
            //if (img != "air")
            addImageToRow(row, img, w, h);
            x += w;
        }
        elem.appendChild(row);
        y += h;
    }
    fitBCG(elem, SCALE);
}
function isTransparent(img) {
    switch (img) {
        case "air":
            return true;
        default:
            return false;
    }
}
function tryCorrectBeltJunctions(blocks, rows, cols, x, y, idx, img) {
    var idxl = getArrayIndex(cols, x - 1, y);
    var idxr = getArrayIndex(cols, x + 1, y);
    var idxu = getArrayIndex(cols, x, y - 1);
    var idxd = getArrayIndex(cols, x, y + 1);
    if (blocks[idxl] == blocks[idxr] && (blocks[idxl] == "belt-left" || blocks[idxl] == "belt-right")) {
        return blocks[idxl];
    }
    if ((blocks[idxl] == "belt-dr" || blocks[idxl] == "belt-ur") && blocks[idxr] == "belt-right") {
        return "belt-right";
    }
    if ((blocks[idxr] == "belt-dl" || blocks[idxr] == "belt-ul") && blocks[idxl] == "belt-left") {
        return "belt-left";
    }
    if ((blocks[idxl] == "belt-dr" || blocks[idxl] == "belt-ur" || blocks[idxl] == "belt-right") && (blocks[idxr] == "belt-left" || blocks[idxr] == "belt-dl" || blocks[idxr] == "belt-ul")) {
        //console.log("Belt @ "+x+", "+y+" has left with right exit and vice versa.");
        return img == "belt-down" || img == "belt-ld" || img == "belt-rd" ? "belt-down" : "belt-up";
    }
    if (blocks[idxu] == blocks[idxd] && (blocks[idxd] == "belt-up" || blocks[idxd] == "belt-down")) {
        return blocks[idxd];
    }
    if ((blocks[idxd] == "belt-lu" || blocks[idxd] == "belt-ru") && blocks[idxu] == "belt-up") {
        return "belt-up";
    }
    if ((blocks[idxu] == "belt-ld" || blocks[idxu] == "belt-rd") && blocks[idxd] == "belt-down") {
        return "belt-down";
    }
    if ((blocks[idxu] == "belt-down" || blocks[idxu] == "belt-rd" || blocks[idxu] == "belt-ld") && (blocks[idxd] == "belt-up" || blocks[idxd] == "belt-ru" || blocks[idxd] == "belt-lu")) {
        //console.log("Belt @ "+x+", "+y+" has up with down exit and vice versa.");
        return img == "belt-right" || img == "belt-dr" || img == "belt-ur" ? "belt-right" : "belt-left";
    }
    return img;
}
function modifyImageLocational(blocks, rows, cols, x, y, idx, img) {
    if (img != null && typeof (img) != "undefined" && img.startsWith("belt") && img.length == 7) { //corner
        img = tryCorrectBeltJunctions(blocks, rows, cols, x, y, idx, img);
    }
    else if (img == "blocker") {
        return "air";
    }
    return img;
}
