function genBelt(rows, cols, blocks) {
    var side1 = getRandomBetween(0, 3); /*
    //var side2 = getRandomBetween(0, 3);
    var y1 = side1 == 0 || side1 == 2 ? (Math.random() < 0.5 ? 0 : rows) : getRandomBetween(0, rows);
    var x1 = side1 == 1 || side1 == 3 ? (Math.random() < 0.5 ? 0 : cols) : getRandomBetween(0, cols);
    //var y2 = side2 == 0 || side2 == 2 ? 0 : getRandomBetween(0, rows);
    //var x2 = side2 == 1 || side2 == 3 ? 0 : getRandomBetween(0, cols);
    var dx = side1 == 1 || side1 == 3 ? 0 : (side1 == );*/
    var x1 = 0;
    var y1 = 0;
    var dx = 0;
    var dy = 0;
    var dir = "";
    switch (side1) {
        case 0:
            x1 = getRandomBetween(0, cols - 1);
            y1 = rows - 1;
            dy = -1;
            dir = "up";
            break;
        case 1:
            x1 = 0;
            y1 = getRandomBetween(0, rows - 1);
            dx = 1;
            dir = "right";
            break;
        case 2:
            x1 = getRandomBetween(0, cols - 1);
            y1 = 0;
            dy = 1;
            dir = "down";
            break;
        case 3:
            x1 = cols - 1;
            y1 = getRandomBetween(0, rows - 1);
            dx = -1;
            dir = "left";
            break;
    }
    var flag = true;
    var x = x1;
    var y = y1;
    var turns = 0;
    while (flag) {
        var dprev = dir;
        var dirp = dir;
        var idx = getArrayIndex(cols, x, y);
        if (blocks[idx] != "air")
            break;
        var randd = Math.random();
        var ch = Math.min(0.35, turns * 0.066);
        var wall = !canBeltMoveFrom(blocks, cols, rows, x, y, dx, dy);
        if (randd < ch || wall) {
            turns = 0;
            var v1 = randd < (wall ? 0.5 : 0.15);
            var down = dy != -1 && canBeltMoveFrom(blocks, cols, rows, x, y, 0, 1);
            var up = dy != 1 && canBeltMoveFrom(blocks, cols, rows, x, y, 0, -1);
            var left = dx != 1 && canBeltMoveFrom(blocks, cols, rows, x, y, -1, 0);
            var right = dx != -1 && canBeltMoveFrom(blocks, cols, rows, x, y, 1, 0);
            var list = [];
            if (down)
                list.push("down");
            if (up)
                list.push("up");
            if (left)
                list.push("left");
            if (right)
                list.push("right");
            if (list.length > 1)
                list = list.filter(item => item != dprev);
            if (list.length > 0) {
                var newdir = list[Math.floor(Math.random() * list.length)];
                switch (newdir) {
                    case "left":
                        dx = -1;
                        dir = "left";
                        dirp = dy == 1 ? "dl" : "ul";
                        dy = 0;
                        break;
                    case "right":
                        dx = 1;
                        dir = "right";
                        dirp = dy == 1 ? "dr" : "ur";
                        dy = 0;
                        break;
                    case "up":
                        dy = -1;
                        dir = "up";
                        dirp = dx == 1 ? "ru" : "lu";
                        dx = 0;
                        break;
                    case "down":
                        dy = 1;
                        dir = "down";
                        dirp = dx == 1 ? "rd" : "ld";
                        dx = 0;
                        break;
                }
            }
            else {
                dx = 0;
                dy = 0;
            }
        }
        else {
            turns++;
        }
        var flag2 = dx == 0 && dy == 0;
        if (flag2) {
            //console.log("Belt from "+dprev+" @ "+x+", "+y+" had nowhere to go");
            //dirp = dir;
        }
        blocks[idx] = "belt-" + dirp;
        x += dx;
        y += dy;
        flag = x >= 0 && x < cols && y >= 0 && y < rows && !flag2;
    }
}
function canBeltMoveFrom(blocks, cols, rows, x, y, dx, dy) {
    var x2 = x + dx;
    var y2 = y + dy;
    var idx = getArrayIndex(cols, x2, y2);
    return x2 < 0 || x2 >= cols || y2 < 0 || y2 >= rows || blocks[idx] == "air" || (blocks[idx].startsWith("belt") && Math.random() < 0.5);
}
function generateDecorations(rows, cols, ground, blocks, tw, th, w, h) {
    var list = [];
    for (var n = 0; n < Math.floor(rows / 4); n++) {
        //var posX = getRandomBetween(6, cols-6);
        //var posY = getRandomBetween(6, rows-6);
        var type = "rock" + getRandomBetween(1, 4);
        var sz = type == "rock1" ? 128 : 64;
        var posX = getRandomBetween(sz / 2, tw - sz / 2);
        var posY = getRandomBetween(sz / 2, th - sz / 2);
        var minX = Math.floor(posX / w);
        var minY = Math.floor(posY / h);
        var maxX = Math.ceil((posX + sz / 2) / w);
        var maxY = Math.ceil((posY + sz / 2) / h);
        var canPlace = true;
        for (var i = minX; i <= maxX && canPlace; i++) {
            for (var k = minY; k <= maxY && canPlace; k++) {
                var idx = getArrayIndex(cols, i, k);
                if (blocks[idx] != "air") {
                    canPlace = false;
                }
            }
        }
        if (canPlace) {
            for (var i = minX; i <= maxX; i++) {
                for (var k = minY; k <= maxY; k++) {
                    var idx = getArrayIndex(cols, i, k);
                    blocks[idx] = "blocker";
                }
            }
            var entry = { image: type, x: posX, y: posY, size: sz, scale: 0.5 };
            //console.log("Placing "+entry.image+" @ "+entry.x+", "+entry.y);
            list.push(entry);
        }
    }
    for (var n = 0; n < Math.max(5, Math.floor(rows / 15)); n++) {
        var rootX = getRandomBetween(256, tw - 256);
        var rootY = getRandomBetween(256, th - 256);
        for (var n2 = 0; n2 < 3; n2++) {
            var building = getRandomBuilding();
            var sz = (building == "turret" || building == "plasma") ? 64 : 128;
            var posX = getRandomBetween(rootX - sz, rootX + sz);
            var posY = getRandomBetween(rootY - sz, rootY + sz);
            var minX = Math.floor(posX / w);
            var minY = Math.floor(posY / h);
            var maxX = Math.ceil((posX + sz / 2) / w);
            var maxY = Math.ceil((posY + sz / 2) / h);
            var canPlace = true;
            for (var i = minX; i <= maxX && canPlace; i++) {
                for (var k = minY; k <= maxY && canPlace; k++) {
                    var idx = getArrayIndex(cols, i, k);
                    if (blocks[idx] != "air") {
                        canPlace = false;
                    }
                }
            }
            if (canPlace) {
                for (var i = minX; i <= maxX; i++) {
                    for (var k = minY; k <= maxY; k++) {
                        var idx = getArrayIndex(cols, i, k);
                        blocks[idx] = "blocker";
                    }
                }
                var entry = { image: building, x: posX, y: posY, size: sz, scale: 0.5 };
                //console.log("Placing "+entry.image+" @ "+entry.x+", "+entry.y);
                list.push(entry);
            }
        }
    }
    for (var n = 0; n < Math.floor(rows / 16); n++) {
        var sz = 192;
        var posX = getRandomBetween(sz / 2, tw - sz / 2);
        var posY = getRandomBetween(sz / 2, th - sz / 2);
        var minX = Math.floor(posX / w);
        var minY = Math.floor(posY / h);
        var maxX = Math.ceil((posX + sz / 2) / w);
        var maxY = Math.ceil((posY + sz / 2) / h);
        var canPlace = true;
        for (var i = minX; i <= maxX && canPlace; i++) {
            for (var k = minY; k <= maxY && canPlace; k++) {
                var idx = getArrayIndex(cols, i, k);
                if (blocks[idx] != "air") {
                    canPlace = false;
                }
            }
        }
        if (canPlace) {
            for (var i = minX; i <= maxX; i++) {
                for (var k = minY; k <= maxY; k++) {
                    var idx = getArrayIndex(cols, i, k);
                    blocks[idx] = "blocker";
                }
            }
            var entry = { image: "spawner", x: posX, y: posY, size: sz, scale: 0.75 };
            //console.log("Placing "+entry.image+" @ "+entry.x+", "+entry.y);
            list.push(entry);
            for (var n2 = 0; n2 < 3; n2++) {
                //posX = getRandomBetween(entry.x-sz, entry.x+sz);
                //posY = getRandomBetween(entry.y-sz, entry.y+sz);
                var ang = getRandomBetween(0, 360) * Math.PI / 180;
                var dr = getRandomDecimalBetween(sz / 3, sz);
                posX = entry.x + sz / 2 + Math.cos(ang) * dr;
                posY = entry.y + sz / 2 + Math.sin(ang) * dr;
                var minX = Math.floor(posX / w);
                var minY = Math.floor(posY / h);
                var maxX = Math.ceil((posX + sz / 2) / w);
                var maxY = Math.ceil((posY + sz / 2) / h);
                var canPlace = true;
                for (var i = minX; i <= maxX && canPlace; i++) {
                    for (var k = minY; k <= maxY && canPlace; k++) {
                        var idx = getArrayIndex(cols, i, k);
                        if (blocks[idx] != "air") {
                            canPlace = false;
                        }
                    }
                }
                if (canPlace) {
                    for (var i = minX; i <= maxX; i++) {
                        for (var k = minY; k <= maxY; k++) {
                            var idx = getArrayIndex(cols, i, k);
                            blocks[idx] = "blocker";
                        }
                    }
                    list.push({ image: "worm", x: posX, y: posY, size: 96, scale: 0.75 });
                }
            }
            for (var n2 = 0; n2 < 9; n2++) {
                //posX = getRandomBetween(entry.x-sz, entry.x+sz);
                //posY = getRandomBetween(entry.y-sz, entry.y+sz);
                var ang = getRandomBetween(0, 360) * Math.PI / 180;
                var dr = getRandomDecimalBetween(sz / 3, sz);
                posX = entry.x + sz / 2 + Math.cos(ang) * dr;
                posY = entry.y + sz / 2 + Math.sin(ang) * dr;
                var mob = "biter" + getRandomBetween(1, 4);
                if (Math.random() < 0.1)
                    mob = "fly";
                list.push({ image: mob, x: posX, y: posY, size: 64, scale: 1 });
            }
        }
    }
    for (var n = 0; n < Math.floor(rows * cols / 12); n++) {
        var sz = 128;
        var posX = getRandomBetween(sz / 2, tw - sz / 2);
        var posY = getRandomBetween(sz / 2, th - sz / 2);
        var blockX = Math.floor((posX + sz / 2) / w);
        var blockY = Math.floor((posY + sz / 2) / h);
        var idx = getArrayIndex(cols, blockX, blockY);
        var canPlace = blocks[idx] == "air";
        if (canPlace) {
            var idx2 = getArrayIndex(Math.floor(cols / 2), Math.floor(blockX / 2), Math.floor(blockY / 2));
            //console.log("Placing "+entry.image+" @ "+entry.x+", "+entry.y+" on tile index "+idx2+" = "+gr	ound[idx2]);
            var sand = ground[idx2].startsWith("dirt") || ground[idx2].startsWith("ice");
            if (sand && xmas)
                continue;
            var entry = { image: "tree" + getRandomBetween(sand ? 1 : 3, sand ? 2 : 5), x: posX, y: posY, size: sz, scale: 1 };
            if (xmas) {
                entry.image = "snowtree" + getRandomBetween(1, 3);
                entry.scale *= 1.5;
            }
            list.push(entry);
        }
    }
    return list;
}
function getRandomBuilding() {
    if (true)
        return "dome";
    var types = ["turret", "plasma", "well", "dome"];
    return types[getRandomBetween(0, types.length - 1)];
}
function genDirt(rows, cols, ground) {
    var x = getRandomBetween(4, cols - 4);
    var y = getRandomBetween(4, rows - 4);
    //console.log("Genning dirt @ "+x+", "+y);
    var s = [2, 5];
    //s[0] = 1;
    //s[1] = 9;
    var rx = getRandomDecimalBetween(s[0], s[1]);
    var ry = getRandomDecimalBetween(s[0], s[1]);
    var mx = Math.max(0, Math.floor(x - rx));
    var my = Math.max(0, Math.floor(y - ry));
    var px = Math.min(cols - 1, Math.floor(x + rx));
    var py = Math.min(rows - 1, Math.floor(y + ry));
    var ms = 0.75;
    var slopeX = getRandomDecimalBetween(-ms, ms);
    var slopeY = getRandomDecimalBetween(-ms, ms);
    for (var i = mx; i <= px; i++) {
        for (var k = my; k <= py; k++) {
            var di = i - x;
            var dk = k - y;
            if (di * di + dk * dk <= rx * ry + 0.5) {
                var dx = i + Math.floor(slopeX * dk);
                var dy = k + Math.floor(slopeY * di);
                var idx = getArrayIndex(cols, dx, dy);
                ground[idx] = xmas ? ("ice" + getRandomBetween(0, 15)) : ("dirt" + getRandomBetween(0, 20));
                //console.log("Placing dirt @ "+dx+", "+dy+" (idx = "+idx);
            }
        }
    }
}
function generateOre(rows, cols, ore) {
    var ret = [];
    for (var n = 0; n < rows * cols / 200; n++) {
        var r = getRandomBetween(2, 9);
        var x = getRandomBetween(r + 2, cols - r - 2);
        var y = getRandomBetween(r + 2, rows - r - 2);
        var mx = Math.max(0, Math.floor(x - r));
        var my = Math.max(0, Math.floor(y - r));
        var px = Math.min(cols - 1, Math.floor(x + r));
        var py = Math.min(rows - 1, Math.floor(y + r));
        var maxtype = 7;
        var f = Math.random();
        var type = "coal";
        if (f < 0.1)
            type = "oil";
        else if (f < 0.4)
            type = "copper";
        else if (f < 0.7)
            type = "iron";
        if (Math.random() < 0.2)
            type = "geothermal_";
        if (type == "oil" || type == "geothermal_") {
            maxtype = type == "oil" ? 3 : 5;
            for (var m = 0; m < r * 2; m++) {
                var dx = getRandomBetween(mx, px);
                var dy = getRandomBetween(my, py);
                var idx = getArrayIndex(cols, dx, dy);
                if (ore[idx] == "air") {
                    ore[idx] = type + getRandomBetween(0, maxtype);
                    if (type == "geothermal_") {
                        ret.push({ x: dx, y: dy });
                        if (dx > 0)
                            ore[getArrayIndex(cols, dx - 1, dy)] = "blocker";
                        if (dx < cols)
                            ore[getArrayIndex(cols, dx + 1, dy)] = "blocker";
                        if (dy > 0)
                            ore[getArrayIndex(cols, dx, dy - 1)] = "blocker";
                        if (dy < rows)
                            ore[getArrayIndex(cols, dx, dy + 1)] = "blocker";
                    }
                }
            }
        }
        else {
            for (var i = mx; i <= px; i++) {
                for (var k = my; k <= py; k++) {
                    var di = i - x;
                    var dk = k - y;
                    if (di * di + dk * dk <= r * r + 0.5) {
                        var idx = getArrayIndex(cols, i, k);
                        if (ore[idx] == "air")
                            ore[idx] = type + getRandomBetween(0, maxtype);
                    }
                }
            }
        }
    }
    return ret;
}
export default function setPageBackground() {
    if (isPhone)
        return;
    let tail = '</svg>';
    let w = 32 * SCALE;
    let h = 32 * SCALE;
    let x = 0;
    let y = 0;
    //var height = findHighestNode(0, document.documentElement.childNodes);
    let body = document.body;
    let html = document.documentElement;
    var rh = document.body.offsetHeight; //Math.max(body.offsetHeight, html.clientHeight, html.offsetHeight, window.innerHeight, document.body.clientHeight);
    var rw = Math.max(window.innerWidth, document.body.clientWidth);
    //console.log(rw+" x "+rh);
    let rows = Math.ceil(rh / h);
    let cols = Math.ceil(rw / w);
    let tw = cols * w;
    let th = rows * h;
    console.log("Generating Factorio tileset with " + cols + " columns and " + rows + " rows");
    var ground = new Array(Math.ceil(cols * rows / 4));
    var ore = new Array(cols * rows);
    var blocks = new Array(cols * rows);
    for (var i = 0; i < cols; i++) {
        for (var k = 0; k < rows; k++) {
            let idx = getArrayIndex(cols, i, k);
            let img = "air";
            blocks[idx] = img;
            ore[idx] = img;
            if (i < cols / 2 && k < rows / 2) {
                let max = xmas ? 15 : (Math.random() < 0.125 ? 15 : 3);
                ground[getArrayIndex(cols / 2, i, k)] = (xmas ? "snow" : "grass") + getRandomBetween(0, max);
            }
        }
    }
    for (var i = 0; i < Math.floor(rows / 6); i++)
        genDirt(rows / 2, cols / 2, ground);
    var geo = generateOre(rows, cols, ore);
    let deco = generateDecorations(rows, cols, ground, blocks, tw, th, w, h);
    for (var i = 0; i < Math.floor(rows / 1.5); i++)
        genBelt(rows, cols, blocks);
    let tag = "background-content";
    let container = document.getElementById(tag);
    if (container == null) {
        container = document.createElement("div");
        container.id = tag;
        console.log("Injecting background element.");
        document.body.prepend(container);
    }
    while (container.firstChild) {
        container.removeChild(container.firstChild);
    }
    //console.log(elem);
    let tiles = document.createElement("div");
    let ores = document.createElement("div");
    let entities = document.createElement("div");
    let decoration = document.createElement("div");
    let glows = document.createElement("div");
    let dark = document.createElement("div");
    container.appendChild(tiles);
    container.appendChild(ores);
    container.appendChild(entities);
    container.appendChild(decoration);
    if (isDarkModeEnabled()) {
        container.appendChild(dark);
    }
    container.appendChild(glows);
    tiles.style.width = rw.toString() + "px";
    tiles.style.height = rh.toString() + "px";
    //tiles.style.position = "static";
    ores.style.width = rw.toString() + "px";
    ores.style.height = rh.toString() + "px";
    ores.style.position = "absolute";
    entities.style.width = rw.toString() + "px";
    entities.style.height = rh.toString() + "px";
    decoration.style.width = rw.toString() + "px";
    decoration.style.height = rh.toString() + "px";
    decoration.style.position = "absolute";
    glows.style.width = rw.toString() + "px";
    glows.style.height = rh.toString() + "px";
    glows.style.position = "absolute";
    glows.style.setProperty("mix-blend-mode", "screen");
    tiles.className = "tile-layer";
    //	ores.className = "tile-layer";
    entities.className = "tile-layer";
    buildImage(tiles, rows / 2, cols / 2, ground, 2);
    buildLooseImage(ores, rows, cols, ore, 32, 0.75, 4, 4);
    buildImage(entities, rows, cols, blocks, 1);
    //container.style.width = rw.toString()+"px";
    //container.style.height = rh.toString()+"px";
    try {
        for (let [type, rgb] of Object.entries(treeColors)) {
            let selector = ".tile-factorio-glowcolor_" + type + "::after";
            let sz = 512;
            let offset = 0; //-sz/2+32;
            document.styleSheets[0].addRule(selector, "width: " + sz + "px;");
            document.styleSheets[0].addRule(selector, "height: " + sz + "px;");
            document.styleSheets[0].addRule(selector, "top: " + offset + "px;");
            document.styleSheets[0].addRule(selector, "left: " + offset + "px;");
            document.styleSheets[0].addRule(selector, "background: radial-gradient(rgba(" + rgb.r + "," + rgb.g + "," + rgb.b + ",255) 0%, rgba(" + rgb.r + "," + rgb.g + "," + rgb.b + ",0) 70%);");
            document.styleSheets[0].addRule(selector, "mix-blend-mode: screen;");
        }
    }
    catch (e) {
        console.log("Could not access ruleset: " + e);
    }
    let domes = [];
    let biters = [];
    for (var i = 0; i < deco.length; i++) {
        var dx = deco[i].x - w / 2;
        var dy = deco[i].y - h / 2;
        var ds = deco[i].size * SCALE;
        if (deco[i].image == "dome") {
            domes.push({ image: "domeglow", x: dx - SCALE * 192, y: dy - SCALE * 160, size: ds * 4 });
            deco[i].scale = 1;
        }
        else if (deco[i].image == "fly" || deco[i].image.startsWith("biter")) {
            biters.push({ x: deco[i].x - deco[i].size / 2, y: deco[i].y - deco[i].size / 2, size: deco[i].size });
        }
        addPositionedImage(decoration, deco[i].image, ds, ds, dx, dy, deco[i].scale);
    }
    for (var i = 0; i < domes.length; i++) {
        addPositionedImage(glows, domes[i].image, domes[i].size, domes[i].size, domes[i].x, domes[i].y);
    }
    let bushes = [];
    for (var n = 0; n < Math.floor(rows / 2); n++) {
        let idx = getRandomBetween(0, 10);
        var bush = "biolumbush" + idx;
        let color = bushColors[idx];
        var dx = getRandomBetween(64, rw - 64);
        var dy = getRandomBetween(64, rh - 64);
        addPositionedImage(decoration, bush, 64, 64, dx, dy, 0.75);
        bushes.push({ color: color, x: dx, y: dy });
    }
    if (isDarkModeEnabled()) {
        dark.style.width = rw.toString() + "px";
        dark.style.height = rh.toString() + "px";
        dark.style.position = "absolute";
        dark.style.backgroundColor = "#395060";
        dark.style.setProperty("mix-blend-mode", "multiply");
        for (var i = 0; i < geo.length; i++) {
            addPositionedImage(dark, "light", 256, 256, geo[i].x * 32 - 48, geo[i].y * 32 - 45, 0.33);
        }
        for (var i = 0; i < biters.length; i++) {
            addPositionedImage(dark, "light", 256, 256, biters[i].x - 16, biters[i].y - 16, 0.25);
        }
        console.log(bushes);
        for (var n = 0; n < bushes.length; n++) {
            let bush = bushes[n];
            for (var i = 0; i < 2; i++) {
                let glow = addPositionedImage(dark, "glowcolor_" + bush.color, 512, 512, bush.x - 22, bush.y - 22, 0.175);
                glow.style.width = "512px";
                glow.style.height = "512px";
            }
        }
        for (var n = 0; n < Math.floor(rows / 3); n++) {
            var plant = "biolumtree" + getRandomBetween(0, 1);
            let color = getRandomMapKey(treeColors);
            var dx = getRandomBetween(64, rw - 64);
            var dy = getRandomBetween(64, rh - 64);
            addPositionedImage(decoration, plant, 64, 64, dx, dy, 1);
            for (var k = -1; k <= 1; k++) {
                for (var i = 0; i < 2; i++) {
                    let glow = addPositionedImage(dark, "glowcolor_" + color, 512, 512, dx + 2, dy + 32 * k, 0.25);
                    glow.style.width = "512px";
                    glow.style.height = "512px";
                }
            }
        }
    }
    fitBCG(decoration, SCALE);
}
